import React from "react";
import styled from "astroturf";

import { getLink } from "./getLink";
import { Link } from "../../styles/contacts";

const AddressLinkStyled = styled(Link, null, "AddressLinkStyled", require("./AddressLink-AddressLinkStyled.module.scss"), "address-link-styled", "addressLinkStyled");

export const AddressLink = ({ children, ...props }) => {
  return (
    <AddressLinkStyled href={getLink()} target="_blank" {...props}>
      {children}
    </AddressLinkStyled>
  );
};
